import Err from "./Error.js";

/**
 * Creates new AbortError.
 * 
 * @classdesc
 * Represents an error when operation was aborted by user.
 * 
 * @constructor UuApp.Error.AbortError
 */
function AbortError() {
  Err.apply(this, arguments);
}
AbortError.prototype = Object.create(Err.prototype);
AbortError.prototype.constructor = AbortError;

export default AbortError;


